/*** bottom of page and @margin-left removes pawdding-left of parent element and centers item**/
.nextpage{

	position: absolute;
	bottom: calc(100% - 95vh);
	display: flex;
    flex-direction: column;
    align-items: center;
    margin-left:-1em;
    width:inherit; color: #BBBFC1;

	p {   font-size: 0.8rem; }
	span { display: inline-block; }
	span:first-child { height:14px; width:14px;}
}


